.rs-select{display:block;width:auto;height:auto;position:relative;box-sizing:border-box;font-family:sans-serif}.rs-select>*{box-sizing:border-box}.rs-select.is-disabled{color:#dbdbdb;background:#eee}.rs-select-button{cursor:pointer;height:40px;line-height:40px;border:1px solid #ddd;background:#f2f2f2;white-space:nowrap;position:relative;padding:0 20px}.rs-select-button::before,.rs-select-button::after{content:'';display:block;position:absolute;right:0;top:0}.rs-select-button::before{width:40px;height:40px}.rs-select-button::after{color:#2199e8;content:'❯';font-size:14px;right:15px;transform:rotate(90deg)}.is-expanded .rs-select-button::after{transform:rotate(-90deg)}.rs-select-options{display:block;position:absolute;width:auto;min-width:100%;height:auto;top:100%;left:0;z-index:10;margin:0;padding:0;list-style:none}.direction-up .rs-select-options{top:auto;bottom:100%}.rs-select-option{cursor:pointer;line-height:26px;border:1px solid #ddd;border-top:none;padding:5px 20px;transition:background 400ms linear}.rs-select-option:hover{background:#ddd}.rs-select-option.is-selected{background:#2199e8;color:#ffffff}.rs-select-option.is-disabled{cursor:default;background:#eee;color:#dbdbdb}
